* {
  margin: 0;
}

.hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.content {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #C0C0C0;
}

.app {
  min-width: 400px;
  border: 2px solid black;
  border-radius: 10px;
  box-sizing: border-box;
  padding: 30px 20px;
  
  display: flex;
  flex-direction: column;
  text-align: left;
  background-color: white;
}

.input {
  width: 100%;
  display: flex;
  gap: 10px;
}

.task-input {
  min-width: 200px;
  height: 45px;
  border-radius: 5px;
  box-sizing: border-box;
  padding-left: 5px;
  padding-right: 5px;
  flex-grow: 1;
  
  font-size: 18px;
  outline: 1px solid #4169E1;
}

.task-input:focus {
  outline: 2px solid black;
}

.add-button {
  width: 70px;
  height: 45px;
  border-radius: 5px;
  font-weight: 600;
  background-color: #4169E1;
  border: none;

  color: white;
  font-size: 17px;
}

.add-button:focus {
  background-color: black;
}

.task-list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 0;
}

.task-block {
  display: flex;
  justify-content: space-between;
  border: 2px solid black;
  border-radius: 5px;
  width: 100%;
  min-height: 30px;
  text-align: left;
  padding: 10px;
  box-sizing: border-box;

  gap: 20px;

  align-items: center;

  background-color: #ADD8E6;
}


.task-block:nth-child(1) {
  margin-top: 20px;
}

.task-content {
  display: flex;
  align-items: center;
  gap: 20px;
}

.button-reset {
  width: 35px;
  height: 35px;

  position: relative;
  border: none;
}

.button-reset::after {
    content: '';
    display: block;
    width: 35px;
    height: 35px;

    background-image: url(../assets/png/delete.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 0;
    right: 0;
}

.checkbox {
  display: none;
}

.fake-checkbox::before {
  content: '';
  display: block;
  width: 20px;
  height: 20px;

  background-image: url(../assets/png/pngwing.com.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: white;
}

.checkbox:checked + .fake-checkbox::before {
  background-image: url(../assets/png/pngwing.com\ \(1\).png);
}